import NavLinks from '../../../reuse_Components/NavLinks/NavLinks';
import Title from '../../../reuse_Components/Title/Title';
import './omparison.scss';
import Dropdown from 'react-dropdown';
import ArrowTitle from '../../../reuse_Components/ArrowTitle/ArrowTitle';
import { useState } from 'react';
import HeartRed from '../../../images/header/HeartRed.svg';
import Shopping from '../../../images/header/Shopping.svg';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import ColorCount from '../Product/reuse/ColorCount/ColorCount';

// let onSelect = (option) => showBusinessCards(option.value);
const responsive = {
  superLargeDesktop: {
    breakpoint: { max: 4000, min: 3000 },
    items: 5,
  },
  desktop: {
    breakpoint: { max: 3000, min: 900 },
    items: 3,
  },
  tablet: {
    breakpoint: { max: 900, min: 480 },
    items: 2,
  },
  mobile: {
    breakpoint: { max: 480, min: 0 },
    items: 2,
  },
};
const options = [
  { label: '', value: 'brand', className: 'omparison-dropdownOption' },
  {
    label: '',
    value: 'franchising',
    className: 'omparison-dropdownOption',
  },
  {
    label: ' ',
    value: 'retailSale',
    className: 'omparison-dropdownOption',
  },
  {
    label: ' ',
    value: 'wholeSale',
    className: 'omparison-dropdownOption',
  },
];
const ButtonGroup = ({ next, previous }) => {
  return (
    <section className='omparison-circles'>
      <section onClick={() => previous()}>
        <div className='omparison-circlesPrev'></div>
      </section>
      <section onClick={() => next()}>
        <div className='omparison-circlesNext'></div>
      </section>
    </section>
  );
};

const omparison = () => {
  const [comparisonProducts, setomparisonProducts] = useState(
    JSON.parse(localStorage.getItem('myComparison'))
  );

  return (
    <section className='Container PageBottom200 omparison'>
      {window.innerWidth < 480 ? (
        <ArrowTitle title='' />
      ) : (
        <NavLinks
          data={[
            { url: 'home', name: '', active: false },
            { url: 'reviews', name: '', active: true },
          ]}
        />
      )}

      <Title title=' ' />

      <section className='omparison-title'>
        <div>3 </div>
        <div onClick={() => localStorage.removeItem('myComparison')}>
           
          <span>&#10006;</span>
        </div>
      </section>

      <section className='omparison-prod'>
        <div className='omparison-prodLeft'>
          <Dropdown
            options={options}
            value={options[0]}
            // onChange={onSelect}
            controlClassName='omparison-dropdownControl'
            menuClassName='omparison-dropdownMenu'
            arrowClosed={<span className='omparison-dropdownClosed' />}
            arrowOpen={<span className='omparison-dropdownOpen' />}
          />

          <div className='omparison-prodLeftCheckbox'>
            <input
              name='notifications'
              component='input'
              type='checkbox'
              id='notifications'
            ></input>
            <label htmlFor='notifications'> </label>
          </div>
        </div>

        <div className='omparison-prodRight'>
          {comparisonProducts.length > 0 ? (
            <Carousel
              responsive={responsive}
              shouldResetAutoplay={false}
              infinite={true}
              customButtonGroup={<ButtonGroup />}
              renderButtonGroupOutside
              arrows={false}
              itemClass='omparison-carouselItem'
            >
              {comparisonProducts.map((info, i) => (
                <section key={i} className='card'>
                  <div className='card-1blockHeart'>
                    <img src={HeartRed} alt='HeartRedIMG' />
                  </div>
                  <div
                    onClick={() => {
                      let newComparisonArr = JSON.parse(
                        localStorage.getItem('myComparison')
                      ).filter((item) => item.data._id !== info.data._id);
                      localStorage.setItem(
                        'myComparison',
                        JSON.stringify(newComparisonArr)
                      );
                      setomparisonProducts(newComparisonArr);
                    }}
                    className='card-1blockDelete'
                  >
                    &#10006;
                  </div>
                  <img
                    className='card-img'
                    src={`https://storage.googleapis.com/${info.data.photos.folder[0]}/${info.data.photos.path[0]}`}
                    alt={info.data.photos.filename}
                  />
                  <div className='card-info'>
                    <div className='card-title'>{info.data.series.name}</div>
                    <div className='card-2block'>
                      <div>{info.data.generalInfo.price} </div>
                      <div className='card-2blockShop'>
                        <img src={Shopping} alt='ShoppingIMG' />
                      </div>
                    </div>
                  </div>
                </section>
              ))}
            </Carousel>
          ) : (
            <section className='omparison-prodRightText'>
                 ...
            </section>
          )}
        </div>
      </section>

      <section className='omparison-tableSection'>
        <div className='omparison-tableSectionTitle'></div>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.brand}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.model}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.generalInfo.productType}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.flavor}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.generalInfo.price} </td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.batteryCapacity}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.liquidVolume}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.nicotinePercentage}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.backlight ? '' : ''}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.charging ? '' : ''}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr key={i}>
                <td>{item.data.series.puffsAmount}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <table className='omparison-table'>
          <thead>
            <tr>
              <th>  </th>
            </tr>
          </thead>
          <tbody>
            {comparisonProducts.map((item, i) => (
              <tr>
                <td>
                  <ColorCount quantity={item.data.generalInfo.quantity} />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </section>
  );
};

export default omparison;
