import './FAQ.scss';
import SearchInput from './../../../reuse_Components/SearchInput/SearchInput';
import NavLinks from '../../../reuse_Components/NavLinks/NavLinks';
import Title from '../../../reuse_Components/Title/Title';
import FAQArticle from './reuse/FAQArticle/FAQArticle';
import ArrowTitle from '../../../reuse_Components/ArrowTitle/ArrowTitle';

const FAQ = () => {
  return (
    <section className='Container PageBottom200 FAQ'>
      {window.innerWidth < 480 ? (
        <ArrowTitle title='' link='' />
      ) : (
        <NavLinks
          data={[
            { url: 'home', name: '', active: false },
            { url: 'about', name: 'F. A. Q.', active: true },
          ]}
        />
      )}

      <Title title='F. A. Q.' />

      <SearchInput />

      <FAQArticle
        article={[
          {
            title: '1.      ?',
            subtitle:
              ',       ,  .      ,      ,       .      ,        .',
          },
          {
            title: '2.   ?',
            subtitle:
              '. ,          .',
          },
          {
            title: '3.    ?',
            subtitle:
              '      ,    ,      .         :    ,  ,      ,   ,     .             .',
          },
          {
            title: '4.      ?',
            subtitle:
              '           .',
          },
          {
            title: '5.       ?',
            subtitle:
              '!     ,           .',
          },
          {
            title: '6.     ?',
            subtitle:
              ',        H5.',
          },
        ]}
      />
    </section>
  );
};

export default FAQ;
