import './Contacts.scss';
import NavLinks from '../../../reuse_Components/NavLinks/NavLinks';
import Title from '../../../reuse_Components/Title/Title';
import { Field, Form } from 'react-final-form';
import { emailValidator, requiredValidator } from '../../../utils/validators';
import ArrowTitle from '../../../reuse_Components/ArrowTitle/ArrowTitle';

const onSubmit = (values) => {
  console.log(values);
};

const Contacts = () => {
  return (
    <section className='Container PageBottom200 ontacts'>
      {window.innerWidth < 480 && <ArrowTitle title='' />}

      {window.innerWidth > 480 && (
        <NavLinks
          data={[
            { url: 'home', name: '', active: false },
            { url: 'reviews', name: '', active: true },
          ]}
        />
      )}

      <Title title='' />

      <section className='ontacts-section'>
        <div>
          <div className='ontacts-title'>  </div>
          <Form
            onSubmit={onSubmit}
            render={({ handleSubmit }) => (
              <form className='ontacts-payForm' onSubmit={handleSubmit}>
                <div className='ontacts-forms'>
                  <Field name='name' validate={requiredValidator}>
                    {({ input, meta }) => (
                      <input
                        className={`ontacts-form ${
                          meta.error && meta.touched && 'ontacts-formError'
                        }`}
                        {...input}
                        type='text'
                        placeholder=''
                      />
                    )}
                  </Field>
                  <Field name='email' validate={emailValidator}>
                    {({ input, meta }) => (
                      <input
                        className={`ontacts-form ${
                          meta.error && meta.touched && 'ontacts-formError'
                        }`}
                        {...input}
                        type='email'
                        placeholder='E-mail'
                      />
                    )}
                  </Field>
                </div>
                <Field name='message'>
                  {({ input }) => (
                    <textarea
                      className='ontacts-formMessage'
                      {...input}
                      type='textarea'
                      placeholder=''
                    />
                  )}
                </Field>
                <button className='BtnGeneral ontacts-formBtn' type='submit'>
                  
                </button>
              </form>
            )}
          />
        </div>
        <div className='ontacts-address'>
          <div className='ontacts-title'> </div>
          <div className='ontacts-addressInfo'>
            <div> : .  , . 2, . 2</div>
            <div> : 09:00 - 19:00</div>
            <div>
               :
              <a className='ontacts-addressInfoTel' href='tel:+74955329521'>
                {' '}
                +7 (495) 532-95-21
              </a>
            </div>
            <div>E-mail: info@vexelgroup.com</div>
          </div>
        </div>
      </section>
    </section>
  );
};

export default Contacts;
